import { AtomButtonTypes } from '@atoms/AtomButton/types';
import { Link } from '@atoms/AtomDropdown/types';
import { AtomIconTypes } from '@atoms/AtomIcon/types';
import Wrapper from '@atoms/AtomWrapper';
import { AtomWrapperTypes } from '@atoms/AtomWrapper/types';
import { css, SerializedStyles } from '@emotion/react';
import { AnimatePresence } from 'framer-motion';
import { FC, useState } from 'react';
import BurgerBUtton from './BurgerButton';
import ContainerPublicLinks from './ContainerLinksPublic';
import HeaderLinkPublic from './LinksPublic';

type Props = {
  Links: Link[];
  buttonProps?: AtomButtonTypes;
  iconProps?: AtomIconTypes;
  customCSS?: SerializedStyles;
  ContainerPublicLinks?: {
    containerProps?: AtomWrapperTypes;
    background?: string;
  };
};

const Sidebar: FC<Props> = (props) => {
  const {
    Links,
    buttonProps,
    children,
    customCSS,
    iconProps,
    ContainerPublicLinks: ContainerStylesPublicLinks,
  } = props;
  const [isShow, setIsShow] = useState(false);

  return (
    <Wrapper
      customCSS={css`
        width: max-content;
        display: none;
        @media screen and (max-width: 1200px) {
          display: block;
        }
      `}
    >
      <BurgerBUtton
        buttonProps={{
          onClick: () => setIsShow(!isShow),
          customCSS: css`
            z-index: 102;
            ${buttonProps?.customCSS}
          `,
        }}
        iconProps={iconProps}
      />
      {isShow && (
        <Wrapper width="max-content">
          <AnimatePresence>
            <ContainerPublicLinks {...ContainerStylesPublicLinks}>
              <HeaderLinkPublic
                data={Links}
                componentsProps={{
                  linksProps: {
                    onClick: () => {
                      setIsShow(false);
                    },
                    customCSS: css`
                      :hover {
                        background: #e8e8e8;
                      }
                    `,
                  },
                }}
                linkedProps={{
                  customcss: css`
                    :hover {
                      background: #f1cece;
                    }
                    ${customCSS}
                  `,
                }}
              />
              {children}
            </ContainerPublicLinks>
          </AnimatePresence>
        </Wrapper>
      )}
    </Wrapper>
  );
};

export default Sidebar;
